import React, { PureComponent } from 'react';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import TelegramBotService from "../../services/telegram-bot-service.js";

const Serivce = new TelegramBotService();
class FormPage extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {login: '', recipient: '', message: ''};

        this.onLoginChange = this.onLoginChange.bind(this);
        this.onRecipientChange = this.onRecipientChange.bind(this);
        this.onMessageChange = this.onMessageChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    onSubmit(event){
        console.log(`${this.state.login},  !`);
        console.log(` \n "${this.state.message}" \n    "${this.state.recipient}"`);
        event.preventDefault();

        Serivce.sendMessage({id:'@reactbottest', msg: JSON.stringify(this.state)});
    }

    onLoginChange(e) {
        this.setState({login: e.target.value});
    }

    onRecipientChange(e) {
        this.setState({recipient: e.target.value});
    }

    onMessageChange(e){
        this.setState({message: e.target.value});
    }

    render() {
        return (
            <div>
                <form onSubmit={this.onSubmit}>
                    <div>
                        <TextField
                            floatingLabelText=""
                            hintText="  "
                            name="login"
                            value={this.state.login}
                            onChange={this.onLoginChange}
                        />
                    </div>
                    <div>
                        <TextField
                            floatingLabelText=""
                            hintText=" "
                            name="recipient"
                            value={this.state.recipient}
                            onChange={this.onRecipientChange}
                        />
                    </div>
                    <div>
                        <TextField
                            floatingLabelText=""
                            /*floatingLabelFixed={true}*/
                            hintText="  "
                            name="message"
                            value={this.state.message}
                            onChange={this.onMessageChange}
                            /*multiLine={true}*/
                            /*rows={2}*/
                        />
                    </div>
                    <div>
                        <RaisedButton type="submit" label="" />
                    </div>
                </form>
            </div>
        );
    }
}

export default FormPage;



